/* non woocommerce product posts */

.post-template-product-single-post {
    .wp-block-separator {
        border: 0;
    }
    .wp-block-button__link {
        background: none;
        text-decoration: none;
        color: #585858;
    }
    .entry {
        .entry-content {
            a {
                text-decoration: none;
            }
        }
        .entry-footer {
            display: none;
        }
    }
    .item-price-outer {
        padding: 20px 0;
        span {
            font-size: 24px;
            color: $primary-color;
        }
    }
}

.specs-outer {
    max-width: 500px;
    margin: auto;
    text-align: left;
    h4 {
        text-align: center;
        margin: 0.75em 0;
    }
    div {
        width: 100%;
        div {
            width: 50%;
            &:first-child {
                text-align: right;
                float: left;
                padding-right: 10px;
            }
        }
        .spec-value {
            display: inline-block;
            text-align: left;
        }
    }

    .wpcf7 {
        max-width: 500px;
        margin: auto;
        .wpcf7-text {
            width: 100%;
        }
    }
}

#enquire {
    max-width: 500px;
    margin: auto;
    text-align: center;
    padding: 30px 0;
    font-size: 1.125em;
}


/* woocommerce product page */

.woocommerce-error, .woocommerce-info, .woocommerce-message {
    background-color: #ffffff;
    border-top: 3px solid $primary-color;
}